* {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
body {
  font-size: 16px;
  line-height: 1.5;
}
::selection {
  background: #0077b6;
}
.disable-select {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.main {
  position: relative;
  background-image: linear-gradient(30deg, #212529, #343a40, #6c757d);
  background-size: 600%;
  background-position: 0 0;
  display: grid;
  grid-template-columns: 1fr;
  /* grid-gap: 1em; */
  align-content: center;
  justify-items: center;
  /* flex-direction: column;
  align-items: center;
  justify-content: space-around; */
  min-width: 35em;
  min-height: 100vh;
  padding-bottom: 1em;
  font-family: "Montserrat";
  animation-duration: 15s;
  animation-iteration-count: infinite;
  animation-name: gradients;
  color: #dee2e6;
}

@keyframes gradients {
  0% {
    background-position: 0 0;
  }
  25% {
    background-position: 50% 0;
  }
  50% {
    background-position: 90% 0;
  }
  60% {
    background-position: 60%;
  }
  75% {
    background-position: 40%;
  }
  100% {
    background-position: 0 0;
  }
}
.heading {
  display: flex;
  align-items: center;
}
.heading h1 {
  font-size: 3.6em;
  font-weight: 700;
}
.heading img {
  width: 5em;
  height: 5em;
  margin-right: 1em;
}

.container {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 1.5em;
  background: #212529;
  width: 60%;
  min-width: 25em;
  max-width: 40em;
  padding: 3em;
  border-radius: 0.5em;
  box-shadow: 0px 6px 1px rgba(0, 0, 0, 0.1), 0px 0px 5px rgba(0, 0, 0, 0.1),
    0px 1px 3px rgba(0, 0, 0, 0.3), 0px 3px 5px rgba(0, 0, 0, 0.2),
    -4px 8px 8px rgba(0, 0, 0, 0.1), -8px 16px 16px rgba(0, 0, 0, 0.37),
    -16px 32px 32px rgba(0, 0, 0, 0.25), -32px 64px 64px rgba(0, 0, 0, 0.25);
}

.speed {
  position: fixed;
  bottom: 0;
  right: 0;
  display: flex;
  background: transparent;
  margin: 1em 1.5em;
  padding: 0.3em 0.5em;
  border: 0.2em solid #6c757d;
  border-radius: 0.2em;
  font-size: 0.8em;
  font-weight: 600;
  color: #dee2e6;
  cursor: pointer;
  transition: 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.speed:hover {
  opacity: 0.8;
}
/* .speed:hover {
  border-color: #2a9d8f;
  color: #2a9d8f;
} */

.speed-active {
  border-color: #e63946;
  color: #e63946;
}
.speed-wrap-circle {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 1em;
}
.speed-circle {
  position: absolute;
  width: 1.5em;
  height: 1.5em;
  border: 0.1em dashed #e63946;
  border-radius: 50%;
  animation: rotation 3s infinite;
}

@keyframes rotation {
  100% {
    transform: rotate(360deg);
  }
}

.message-danger {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* position: absolute; */
  top: 0;
  left: 0;
  right: 0;
  bottom: auto;
  padding: 1em;
  /* margin: 1em 0; */
}
.message-danger h5 {
  font-size: 1.1em;
  font-weight: 500;
}
.message-danger-small {
  font-size: 0.7em;
  font-weight: 500;
}

.help-mask {
  position: fixed;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #000000d0;
  z-index: 9999;
  cursor: pointer;
}

.help-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5em 0;
  text-align: center;
}
.help-wrap h2 {
  font-size: 3.2em;
  font-weight: 700;
  margin-bottom: 2em;
}
@media only screen and (max-width: 700px) {
  .help-wrap h2 {
    font-size: 2.2em;
  }
}
@media only screen and (max-width: 500px) {
  .help-wrap h2 {
    font-size: 1.8em;
  }
}
.help-wrap p {
  width: 20em;
}
.opt-wrap {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 2em;
}

.opt-input {
  display: flex;
}

.opt-input > input[type="text"] {
  background: transparent;
  width: 100%;
  border-bottom: 1px solid #6c757d;
  padding: 0.2em 0.5em;
  box-sizing: border-box;
  color: #e9ecef;
  transition: 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
}
label {
  margin-right: 1em;
  color: #adb5bd;
}

.opt-input > input[type="text"]:focus {
  outline: none;
  border-bottom: 0.1em solid #1e6091;
}
.opr-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
}
.opr-help {
  margin-left: 1em;
  cursor: pointer;
}

.operation {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 1em;
}

.opt-wrapInput,
.opt-wrapOutput {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 1em;
  justify-items: center;
}

.operation button {
  background: transparent;
  width: 7em;
  padding: 0.3em 0.4em;
  border: 0.1em solid #adb5bd;
  border-radius: 0.2em;
  color: #adb5bd;
  cursor: pointer;
  transition: 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.operation button:hover {
  color: #74c69d;
  border: 0.1em solid #52b788;
}
.operation textarea {
  background: #dee2e6;
  width: 70%;
  min-height: 6em;
  padding: 0.5em;
  border-radius: 0.5em;
  box-sizing: border-box;
  resize: none;
}

.operation textarea:focus {
  outline: none;
  border: 0.15em solid #1e6091;
}

.popup-wrap {
  display: grid;
  grid-template-columns: 0.25fr 1.5fr 0.25fr;
  grid-gap: 1em;
  align-items: center;
  justify-items: center;
  background: #6c757dc0;
  min-width: 10em;
  padding: 0.3em 0.7em;
  border-radius: 0.3em;
  color: #161a1d;
  transition: 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.popup-wrap:hover {
  background: #6c757de8;
}

.popup-emoji {
  display: none;
  font-size: 1.5em;
}
.popup-active {
  display: block;
}

.popup-info {
  border: 0.1em solid #1e6091;
}
.popup-success {
  border: 0.1em solid #52b788;
}
.popup-error {
  border: 0.1em solid #e63946;
}

.popup-close {
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  opacity: 0.6;
  font-size: 1.2em;
  cursor: pointer;
  transition: 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.popup-close:hover {
  opacity: 0.9;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
